@import '@infomat/uikit/src/themeUtils';

.container {
	width: themeSpacing(32.5);
	max-width: themeSpacing(32.5);
	min-width: themeSpacing(32.5);
	display: flex;
	flex-direction: column;
	justify-items: stretch;
	gap: themeSpacing(1.5);
}

.subContainer {
	display: flex;
	flex-direction: column;
	justify-items: stretch;
	gap: themeSpacing(1);
}
